#welcome{
  font-family: 'Sora', sans-serif;
  color: rgb(255, 255, 255);
  display: flex;
  margin-top: 15%;
  margin-left: 5%;
  font-size: 40px;
}


#a1{
  font-family: 'Sora', sans-serif;
  color: rgb(255, 255, 255);
  margin-top: 5%;
  margin-left: 5%;
  font-size: 25px;
}

#a2{
  font-family: 'Sora', sans-serif;
  color: rgb(255, 255, 255);
  margin-left: 5%;
  font-size: 40px;
  text-decoration: none;
}

#projbut{
  margin-top: 12%;
  margin-left: 5%;
  cursor: pointer;
  font-size: 1rem;
}

#projbuthid{
  position: relative;
  text-align: center;
  cursor: pointer;
  font-size: 1rem;
}

#hidcontinf{
  margin-top: 70%;
  margin-left: 5%;
}

#cont{
  font-family: 'Sora', sans-serif;
  color: rgb(255, 255, 255);
  margin-left: 5%;
  font-size: 23px;
}

#hidden{ 
  margin-top: 5%;
  font-size: 15px;
  margin-left: 5%;
  font-family: 'Sora', sans-serif;
}
@media only screen and (min-width: 768px){
  #projbuthid{
    display: none;
  }
  #hidcontinf{
  display: none;
  }
  #button1{
    background: none!important;
    border: none;
    padding: 0!important;
    /*optional*/
    font-family: 'Montserrat', sans-serif;
    /*input has OS specific font-family*/
    color: white;
    cursor: pointer;
    font-size: 20px;
    margin-left: 10px;
    }
  #button1:hover {
    cursor: hand;
    text-decoration: underline;
  }

  #welcome{
    font-family: 'Sora', sans-serif;
    color: rgb(255, 255, 255);
    display: flex;
    margin-top: 15%;
    margin-left: 5%;
    font-size: calc(.5em + 3vw);
  }

  #a{
    font-family: 'Sora', sans-serif;
    color: rgb(255, 255, 255);
    margin-top: 0%;
    margin-left: 5%;
    font-size: calc(.5em + 1vw);
  }

  #a1{
    font-family: 'Sora', sans-serif;
    color: rgb(255, 255, 255);
    margin-top: 1%;
    margin-left: 5%;
    margin-bottom: 1%;
    font-size: 50px;
  }

  #linked{
    font-family: 'Sora', sans-serif;
    color: rgb(255, 255, 255);
    display: flex;
    margin-top: 3%;
    margin-left: 5%;
    font-size: calc(.5em + 3vw);
  }

  #projbut{
    margin-top: 0.5%;
    margin-left: 5%;
    cursor: pointer;
  }

  .animated {
    font-size: 60px;
    font-weight: 900;
    text-decoration: none;
    background: linear-gradient(to right, #CCC 50%, #FF5858 50%, #CC2E5D);
    background-size: 200%;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: 0.5s ease-out;
   }
   
   .animated:hover {
    background-position: -100%;
   }

   #bt1{
    padding: 1rem;
    font-family: inherit;
    font-weight: bold;
    font-size: 1rem;
    margin: 1rem;
    border: 2px solid #d50000;
    background: transparent;
    color: #d50000;
    border-radius: 0.2rem;
    transition: background 200ms ease-in, color 200ms ease-in;
  }

  #hidden{
    display: none;
  }
  #a2{
    display: none;
  }
}
  
